export default class FormUtils {
  static isStrValid(str) {
    return !(str === undefined || str === null || str.length === 0)
  }

  static isPoneAvailable(str) {
    const myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
    return myreg.test(str)
  }

  static objToFormData(obj) {
    let form = new FormData()
    for (let key in obj) {
      if (obj[key] != null) {
        form.append(key, obj[key])
      }
    }
    return form
  }

  static deepCopy(obj) {
    if (typeof obj !== 'object') {
      return obj
    }
    let newobj = {}
    for (var attr in obj) {
      newobj[attr] = this.deepCopy(obj[attr])
    }
    return newobj
  }

  static dataToStr(date) {
    return this.dateFtt("yyyy-MM-dd", date);
  };

  static crtTimeFtt(value) {
    let crtTime = new Date(value);
    return this.dateFtt("yyyy-MM-dd hh:mm", crtTime);//直接调用公共JS里面的时间类处理的办法
  }

  static dateFtt(fmt, date) { //author: meizz
    var o = {
      "M+": date.getMonth() + 1,                 //月份
      "d+": date.getDate(),                    //日
      "h+": date.getHours(),                   //小时
      "m+": date.getMinutes(),                 //分
      "s+": date.getSeconds(),                 //秒
      "q+": Math.floor((date.getMonth() + 3) / 3), //季度
      "S": date.getMilliseconds()             //毫秒
    };
    if (/(y+)/.test(fmt))
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
      if (new RegExp("(" + k + ")").test(fmt))
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  }

}
