<style scoped>
    a {
        color: #2d8cf0;
        font-size: 0.8rem;
    }
</style>

<template>
    <Card shadow class="account-page">
        <h1 class="account-page-title">
            欢迎注册创享梦空间
        </h1>

        <Form ref="formValidate" :label-width="90" style="width: 90%;margin-top: 20px" :model="formValidate"
              :rules="ruleValidate">

            <FormItem prop="mobile" label="手机号">
                <Input v-model="formValidate.mobile"></Input>
            </FormItem>

            <FormItem label="密码" prop="password">
                <Input v-model="formValidate.password" type="password"></Input>
            </FormItem>

            <FormItem label="短信验证码" prop="password">

                <Row>
                    <Col :span="10">
                        <Input v-model="formValidate.code"></Input>
                    </Col>
                    <Col :span="14" style="text-align: right">
                        <SmsCode :phone="formValidate.mobile"></SmsCode>
                    </Col>
                </Row>

            </FormItem>

            <FormItem label="验证码" prop="verify">
                <Row>
                    <Col :span="8">
                        <Input v-model="formValidate.verify"></Input>
                    </Col>
                    <Col :span="16">
                        <Verify ref="verify"></Verify>
                    </Col>
                </Row>
            </FormItem>
            <!--<FormItem>-->
            <!--</FormItem>-->
        </Form>
        <div style="padding: 0 20px">
            <Button type="primary" @click="handleSubmit('formValidate')" long>注册</Button>
            <Row style="margin-top: 5px">
                <Col :span="24" style="text-align: center">
                    <router-link :to="{name:'login'}">已有账号 , 立即登录</router-link>
                </Col>
            </Row>
        </div>
    </Card>
</template>

<script>
  import Verify from './components/verify';
  import SmsCode from './components/smsCode';
  import Api from './api';

  export default {
    name: 'login',
    components: {
      Verify,
      SmsCode
    },
    data() {
      return {
        formValidate: {
          mobile: '',
          password: '',
          verify: '',
          code: '',
        },
        ruleValidate: {
          mobile: [
            {
              required: true,
              message: '手机号不能为空',
              trigger: 'blur',
            },
          ],
          password: [
            {
              required: true,
              message: '密码不能为空',
              trigger: 'blur'
            },
          ],
          verify: [
            {
              required: true,
              message: '图片验证码不能为空',
              trigger: 'blur'
            },
          ],
          code: [
            {
              required: true,
              message: '短信不能为空',
              trigger: 'blur'
            },
          ],
        }
      };
    },
    mounted() {
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (!valid) {
            this.$Message.error('请将表单填写完整');
          } else {
            this.login();
          }
        });
      },
      login() {
        if (!this.$formUtil.isPoneAvailable(this.formValidate.mobile)) {
          this.message('请输入正确的手机号');
          return;
        }
        if (!this.$formUtil.isStrValid(this.formValidate.password)) {
          this.message('请输入密码');
          return;
        }
        if (!this.$formUtil.isStrValid(this.formValidate.verify)) {
          this.message('请输入图形验证码');
          return;
        }
        if (!this.$formUtil.isStrValid(this.formValidate.code)) {
          this.message('请输入短信验证码');
          return;
        }

        Api.register(this.formValidate, res => {
          this.$store.dispatch('login', res.data);
          this.$router.push({
            name: 'userInfo',
            params: { name: this.$store.state.user.mobile }
          });
        }, () => {
          this.$refs.verify.refImg();
        });
      },
      message(text) {
        // this.$message.error(
        //     text,
        // );
      }
    },

  };
</script>
