import Api from '@/js/api/baseApi';

export default class {
  static register(data, callback, errorBack) {
    Api.request(this.uri() + 'register', callback, errorBack, data, null);
  }

  static recoverPass(data, callback, errorBack) {
    Api.request(this.uri() + 'recover_pass', callback, errorBack, data, null);
  }

  static changePass(data, callback, errorBack) {
    Api.request(this.uri() + 'change_pass', callback, errorBack, data, null);
  }

  static login(data, callback, errorBack) {
    Api.request(this.uri() + 'login', callback, errorBack, data, null);
  }

  static logout(callback, errorBack) {
    Api.request(this.uri() + 'logout', callback, errorBack, null, null, false);
  }

  static smsCode(phone, callback, errorBack) {
    let params = {
      mobile: phone
    };
    Api.request(Api.url() + 'sms/attain', callback, errorBack, null, params);

  }

  static uri() {
    return Api.url() + 'user/';
  }
}
