import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/home/Home.vue';
import Space from './views/space/index.vue';
import SpacePlace from './views/space/place.vue';
import News from './views/news/index.vue';
import Service from './views/service/index.vue';
import NewsDetail from './views/news/detail.vue';
import Login from './views/account/login';
import Account from './views/account/account';
import Register from './views/account/register';
import Forget from './views/account/forget';
import UserInfo from './views/team/index';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/space',
      name: 'space',
      component: Space,
    },
    {
      path: '/space/:id',
      name: 'spacePlace',
      component: SpacePlace,
    },
    {
      path: '/news',
      name: 'news',
      component: News,
    },
    {
      path: '/news/:id',
      name: 'newsDetail',
      component: NewsDetail,
    },
    {
      path: '/service',
      name: 'service',
      component: Service,
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue'),
    },
    {
      path: '/account',
      name: 'account',
      component: Account,
      meta: {
        name: 'account',
      },
      children: [
        {
          path: 'login',
          name: 'login',
          component: Login,
          meta: {
            name: 'account',
          },
        },
        {
          path: 'register',
          name: 'register',
          component: Register,
          meta: {
            name: 'account',
          },
        },
        {
          path: 'forget',
          name: 'forget',
          component: Forget,
          meta: {
            name: 'account',
          },
        }
      ]
    },
    {
      path: '/team/contact',
      name: 'contact',
      component: () => import('./views/team/welcome.vue')
    },
    {
      path: '/team',
      component: UserInfo,
      children: [
        {
          path: '',
          name: 'teamInfo',
          component: () => import('./views/team/pages/team/index.vue'),
          meta: {
            index: 'teamInfo',
            name: 'user'
          }
        },
        {
          path: 'edit',
          name: 'teamEdit',
          component: () => import('./views/team/pages/team/edit.vue'),
          meta: {
            index: 'teamInfo',
            name: 'user'
          }
        },
        {
          path: 'member/:id',
          name: 'memberDetail',
          component: () => import('./views/team/pages/team/member/detail.vue'),
          meta: {
            index: 'teamInfo',
            name: 'user'
          }
        },
        {
          path: 'memberEdit',
          name: 'memberEdit',
          component: () => import('./views/team/pages/team/member/edit.vue'),
          meta: {
            index: 'teamInfo',
            name: 'user'
          }
        },
        {
          path: 'linkmanEdit/:type',
          name: 'linkmanEdit',
          component: () => import('./views/team/pages/team/linkman/edit.vue'),
          meta: {
            index: 'teamInfo',
            name: 'user'
          }
        },
        {
          path: 'reservation',
          name: 'reservation',
          component: () => import('./views/team/pages/reservation/index.vue'),
          meta: {
            index: 'reservation',
            name: 'user'
          }
        },
        {
          path: 'reservation/:id',
          name: 'reservationDetail',
          component: () => import('./views/team/pages/reservation/reservation.vue'),
          meta: {
            index: 'reservation',
            name: 'user'
          }
        },
        {
          path: 'reservationMy',
          name: 'reservationMy',
          component: () => import('./views/team/pages/reservation/my.vue'),
          meta: {
            index: 'reservation',
            name: 'user'
          }
        },
        {
          path: 'repair',
          name: 'repair',
          component: () => import('./views/team/pages/repair/index.vue'),
          meta: {
            index: 'repair',
            name: 'user'
          }
        },
        {
          path: 'charge',
          name: 'charge',
          component: () => import('./views/team/pages/charge/index.vue'),
          meta: {
            index: 'charge',
            name: 'user'
          }
        },
      ]
    }
  ],
});
