<style scoped>
    .title {
        height: 100px;
        padding: 20px 0;
        /*background-color: #666666;*/
    }

    .nav {
        text-align: right;
    }

    .link {
        line-height: 60px;
        font-size: 14px;
        font-weight: 500;
        font-family: Arial;
        color: #C9C9C9;
        cursor: pointer;
    }

    .link:hover {
        color: white;
    }

    .nav .link {
        margin-right: 30px;
    }
</style>
<template>
    <Row class="title"
         :style="{'background-color':($route.name==='home'||$route.name==='about'||$route.meta.name==='account')?this.op:'rgba(54,54,54,1)'}">
        <Col :span="6">
            <img src="../assets/logo.png" height="60"/>
        </Col>
        <Col :span="14" class="nav">
            <router-link class="link" :style="{'color':$route.name==='home' ?'#FFD400':''}" :to="{name:'home'}">首页
            </router-link>
            <router-link class="link" :style="{'color':$route.name==='space' ?'#FFD400':''}" :to="{name:'space'}">入驻空间
            </router-link>
            <router-link class="link"
                         :style="{'color':($route.name==='news'||$route.name==='newsDetail') ?'#FFD400':''}"
                         :to="{name:'news'}">
                最新资讯
            </router-link>
            <router-link class="link" :style="{'color':$route.name==='service' ?'#FFD400':''}" :to="{name:'service'}">
                创业服务
            </router-link>
            <router-link class="link" :style="{'color':$route.name==='about' ?'#FFD400':''}" :to="{name:'about'}">
                关于空间
            </router-link>
        </Col>
        <Col :span="4">
            <router-link v-if="this.$store.state.user==null" class="link"
                         :style="{'color':$route.meta.name==='account' ?'#FFD400':''}" :to="{name:'login'}">登录/注册
            </router-link>

            <Dropdown @on-click="select" v-if="this.$store.state.user!=null">
                <a class="link" href="javascript:void(0)"
                   :style="{'color':$route.meta.name==='user' ?'#FFD400':''}">
                    {{ this.$store.state.user.mobile }}
                </a>
                <DropdownMenu slot="list">
                    <DropdownItem name="teamInfo">
                        个人中心
                    </DropdownItem>
                    <DropdownItem name="login">
                        注销登录
                    </DropdownItem>
                </DropdownMenu>
            </Dropdown>


        </Col>
    </Row>
</template>

<script>
  export default {
    data() {
      return {
        op: 'rgba(102,102,102,0)',

      };
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);

    },
    methods: {
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let opt = scrollTop / 460;
        this.op = 'rgba(54,54,54,' + opt + ')';
      },
      select(name) {
        this.$router.push({
          name: name,
          params: {
            phone: this.$store.state.user.mobile
          }
        });
      }
    },
  };
</script>
