/* eslint-disable import/first */
import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import iView from 'iview';
import 'iview/dist/styles/iview.css';
import Amn from 'wowjs/css/libs/animate.css';
import formUtil from './js/utils/form';

Vue.prototype.$formUtil = formUtil;

require('./main.css');
require('./index.css');
require('./public.css');

Vue.use(iView);
Vue.config.productionTip = false;
Vue.prototype.$url = 'http://service.fh.81youxi.com/';


new Vue({
  router,
  store,
  Amn,
  render: h => h(App),
}).$mount('#app');

router.afterEach(() => {
  window.scrollTo(0, 0);
});
