    content: '2018年是上海科技企业孵化器三十周年，上海科技企业孵化器在培育优秀企业、助推科技创新、促进产业发展等方面取得了丰硕成果。本次论坛邀请了来自大企业、特色孵化器和科创企业的四位代表，分享故事、陈述观点、共话未来。大会设立4大类奖项，通过对一批孵化载体、孵化人、孵化企业和合作机构进行表彰和宣传，呈现了上海创新创业服务行业中各方合力、凝聚众智、传承发展的生动局面，发扬了积极推进独具上海特色的双创孵化事业的进取精神。其中，浦软孵化器荣获由上海市科技创业中心颁发的“上海科技企业孵化器30周年杰出成就孵化器奖”，浦软孵化器企业服务部经理邵晓怡被评为“孵化服务标兵”',
