<style scoped>
    a {
        color: #2d8cf0;
        font-size: 0.8rem;
    }
</style>

<template>
    <Card shadow class="account-page">
        <h1 class="account-page-title">
            欢迎登录创享梦空间
        </h1>
        <Form ref="formValidate" :label-width="80" style="width: 90%;margin-top: 20px" :model="formValidate"
              :rules="ruleValidate">
            <FormItem prop="mobile" label="手机号">
                <Input v-model="formValidate.mobile"></Input>
            </FormItem>
            <FormItem label="密码" prop="password">
                <Input v-model="formValidate.password" type="password"></Input>
            </FormItem>

            <FormItem label="验证码" prop="verify">
                <Row>
                    <Col :span="8">
                        <Input v-model="formValidate.verify"></Input>
                    </Col>
                    <Col :span="16">
                        <Verify ref="verify"></Verify>
                    </Col>
                </Row>
            </FormItem>
            <!--<FormItem>-->
            <!--</FormItem>-->
        </Form>
        <div style="padding: 0 20px">
            <Button type="primary" @click="handleSubmit('formValidate')" long>提交</Button>
            <Row style="margin-top: 5px">
                <Col :span="12" style="text-align: left">
                    <router-link :to="{name:'register'}">立即注册</router-link>
                </Col>
                <Col :span="12" style="text-align: right">
                    <router-link :to="{name:'register'}"> 忘记密码?</router-link>
                </Col>
            </Row>
        </div>
    </Card>
</template>

<script>
  import Verify from './components/verify';
  import Api from './api';

  export default {
    name: 'login',
    components: {
      Verify
    },
    data() {
      return {
        formValidate: {
          mobile: '',
          password: '',
          verify: '',
        },
        ruleValidate: {
          mobile: [
            {
              required: true,
              message: '手机号不能为空',
              trigger: 'blur',
            },
          ],
          password: [
            {
              required: true,
              message: '密码不能为空',
              trigger: 'blur'
            },
          ],
          verify: [
            {
              required: true,
              message: '验证码不能为空',
              trigger: 'blur'
            },
          ],
        }
      };
    },
    mounted() {
      if (this.$store.state.user != null) {
        this.$store.dispatch('logout');
        Api.logout();
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (!valid) {
            this.$Message.error('请将表单填写完整');
          } else {
            this.login();
          }
        });
      },
      login() {
        if (!this.$formUtil.isPoneAvailable(this.formValidate.mobile)) {
          this.message('请输入正确的手机号');
          return;
        }
        if (!this.$formUtil.isStrValid(this.formValidate.password)) {
          this.message('请输入密码');
          return;
        }
        if (!this.$formUtil.isStrValid(this.formValidate.verify)) {
          this.message('请输入图形验证码');
          return;
        }
        Api.login(this.formValidate, res => {

          this.$store.dispatch('login', res.data);

          if (parseInt(res.data.is_enter) === 0) {
            this.$router.push({
              name: 'contact',
            });
          } else {
            this.$router.push({
              name: 'teamInfo',
            });
          }


        }, () => {

          this.$refs.verify.refImg();

        });
      },
      message(text) {
        // this.$message.error(
        //     text,
        // );
      }
    },

  };
</script>
